<template>
	<div id='left_attach'>
		<!-- 顶部START -->
		<div class='start'>
			<div class='title'>START</div>
		</div>
		<!-- 下面的目录 -->
		<div class='catalogs'>
			<Catagory />
		</div>
	</div>
</template>
<script>
	import Catagory from '../page/cata'
	export default {
		name: 'leftAttach',
		data() {
			return {

			}
		},
		components: {
			Catagory
		}
	}

</script>
<style scoped="true">
	#left_attach {
		top: 0px;
		left: 0px;
		height: 100%;
		position: fixed;
		width: 25%;
		min-width: 300px;
		overflow: hidden;
		text-align: center;
		opacity: 0.8;
		background-color: black;
	}

	.start {
		height: 125px;
		color: #FFFFFF;
		background-color: #45489A;
		padding-top: 25px;
	}

	.start .title {
		width: 80%;
		height: 100px;
		line-height: 100px;
		text-align: center;
		margin: 0 auto;
		border: 1px solid #9FA1CA;
		font-size: 28px;
		font-weight: bolder;
	}

	.catalogs {
		width: 100%;
		height: 100%;
	}

</style>



