import Vue from 'vue'
import Router from 'vue-router'
import MainIndex from '@/components/page/index'
import Page2 from '@/components/page/page2'
import readblog from '@/components/page/readblog'

Vue.use(Router)

export default new Router({
	routes: [
		{
			path: '/',
			name: 'MainIndex',
			component: MainIndex
    },
		{
			path: '/readblog/:id',
			name: 'readblog',
			component: readblog,
			props: true 	// 开启插槽
    }
  ]
})



