<template>
	<div id='main_index'>
		<NavHeader />
		<LeftAttach />
		<PageOne />
		<PageTwo />
		<PageThree />
		<PageFour />
		<Footer />
	</div>
</template>
<script>
	import NavHeader from '../header/navheader'
	import LeftAttach from '../left/left'
	import PageOne from './page1'
	import PageTwo from './page2'
	import PageThree from './page3'
	import PageFour from './page4'
	import Footer from '../footer/footer'
	export default {
		name: 'index',
		data() {
			return {

			}
		},
		components: {
			NavHeader,
			LeftAttach,
			PageOne,
			PageTwo,
			PageThree,
			PageFour,
			Footer
		}
	}

</script>
<style scoped="true">
	#main_index {
		top: 0px;
		left: 0px;
		width: 100%;
		min-height: 100%;
		min-width: 1200px;
		position: absolute;
	}
	
</style>



